import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Jobs from '../views/Jobs.vue'

Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    children: [
      {
        path: '',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/Home/index.vue')
      },
      {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
      },
      {
        path: '/jobs',
        name: 'job-list',

        component: () => import('../views/Jobs.vue')
      },
      {
        path: '/signin',
        name: 'Signin',
        component: () => import('../views/Sign/user.vue')
      },
      {
        path: '/register',
        name: 'Register',
        component: () => import('../views/Register/user.vue')
      },
      {
        path: '/employer/sign',
        name: 'Employer-signin',
        component: () => import('../views/EmployerSign')
      },
      {
        path: '/jobs/description/:id',
        name: 'Jobs-descriptions',
        component: () => import ('../views/Jobs/description.vue')
      },
    ]
  },
  {
    path: '/uploader',
    name: 'uploader',
    component: () => import ('../views/Uploader/index.vue'),
    children: [
        {
          path: '',
          name: 'uploader-form',
          component: () => import('../views/Uploader/start.vue')
        },
        {
          path: 'resume',
          name: 'uploader-resume',
          component: () => import ('../views/Uploader/resume.vue')
        }
    ]
  },
  {
    path: '/account',
    name: 'account',
    component: () => import ('../views/Account/index.vue'),
  },
  {
    path: '/commingsoon',
    name: 'comming-soon',
    component: () => import ('../views/Comingsoon.vue')
  },
  {
    path: '/my-account',
    name: 'My-account',
    component: () => import ('../views/Account/index.vue'),
    children: [
      {
          path: '',
          name: 'user-account',
          component: () => import ('../views/Account/userAccount.vue'),
      },
      {
        path: 'uploader',
        name: 'Account-uploader',
        component: () => import ('../views/Resume/index.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
