
const state = {
	database: null,
	data: []
}

const mutations = {
	init ( state, data) {
  		state.database = data.database;
  	},
  	load (state, data) {
  		state.data = [];
  		for( var i = 0; i < data.data.length; i++) {
  			state.data.push({
  				firstname: data.data[i][0],
  				lastname: data.data[i][1],
  				email: data.data[i][2],
  				password: data.data[i][3]
  			});
  		}
  	},
  	save( state, data) {
  		state.data.push({
  			firstname: data.data.firstname,
  			lastname: data.data.lastname,
  			email: data.data.email,
  			password: data.data.email
  		});
  	}
}

const actions = {
	 init(context) {
        (new Sqlite("my.db")).then(db => {
            db.execSQL("CREATE TABLE IF NOT EXISTS users (id INTEGER PRIMARY KEY AUTOINCREMENT, firstname TEXT, lastname TEXT, email TEXT, password TEXT)")
            .then(id => {
                context.commit("init", { database: db });
            }, error => {
                console.log("CREATE TABLE ERROR", error);
            });
        }, error => {
            console.log("OPEN DB ERROR", error);
        });
    },
    insert(conext, data) {
        context.state.database.execSQL("INSERT INTO users (firstname, lastname, email, password) VALUES (?, ?, ?, ?)", [data.firstname, data.lastname, data.email, data.password])
        .then(id => {
            context.commit("save", { data: data });
        }, error => {
            console.log("INSERT ERROR", error);
        });
    },
    query(context) {
        context.state.database.all("SELECT firstname, lastnamemm, email FROM users", []).then(result => {
            context.commit("load", { data: result });
        }, error => {
            console.log("SELECT ERROR", error);
        });
    }
}


export default {
  state,
  // getters,
  actions,
  mutations
}